}
.checkbox input:checked ~ .checkbox__indicator {
      background: -webkit-gradient(linear, left top, right bottom, from(#0366D6), to(#0372ef));
      background: linear-gradient(to bottom right, #0366D6, #0372ef);
}
.checkbox input:checked ~ .checkbox__indicator:after {
        display: block;
}
.checkbox__indicator {
    position: absolute;
    top: 0px;
    left: 0;
    height: 17px;
    width: 17px;
    background: -webkit-gradient(linear, left top, right bottom, from(#dedede), to(#eee));
    background: linear-gradient(to bottom right, #dedede, #eee);
    border-radius: 3px;
}
.checkbox__indicator:after {
      content: '';
      position: absolute;
      display: none;
      left: 7px;
      top: 4px;
      width: 4px;
      height: 8px;
      border: solid #fff;
      border-width: 0 2px 2px 0;
      -webkit-transform: rotate(45deg);
              transform: rotate(45deg);
}
